import React from 'react';
import p5 from 'p5';

export class Control extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        a: 0.5,
        b: 2,
        c: 120
      };
      this.handleInputChange = this.handleInputChange.bind(this);
    }
  
    renderButtonA() {
      return (
        <div>
          Branch Factor:
          <button
            className="btn"
            onClick={() =>
          this.setState({
                a: 0.66
              })
            }
          >
            {0.66}
          </button>
        </div>
      );
    }
  
    renderSliderA() {
      return (
        <input
          className="slider"
          name="a"
          type="range"
          min="0.4"
          max="0.74"
          step="0.01"
          value={this.state.a}
          onChange={this.handleInputChange}
        ></input>
      );
    }
  
    renderButtonB() {
      return (
        <div>
          Branch limit:
          <button
            className="btn"
            onClick={() =>
              this.setState({
                b: 2
              })
            }
          >
            {2}
          </button>
        </div>
      );
    }
  
    renderSliderB() {
      return (
        <input
          className="slider"
          name="b"
          type="range"
          min="1"
          max="3"
          step="0.5"
          value={this.state.b}
          onChange={this.handleInputChange}
        ></input>
      );
    }
  
    renderButtonC() {
      return (
        <div>
          Stem length:
          <button
            className="btn"
            onClick={() =>
              this.setState({
                c: 120
              })
            }
          >
            {120}
          </button>
        </div>
      );
    }
  
    renderSliderC() {
      return (
        <input
          className="slider"
          name="c"
          type="range"
          min="100"
          max="150"
          step="1"
          value={this.state.c}
          onChange={this.handleInputChange}
        ></input>
      );
    }
  
    handleInputChange(event) {
      const target = event.target;
      const name = target.name;
      this.setState({
        [name]: target.value
      });
    }
  
    render() {
      return (
        <div>
          <div>
            {this.renderButtonA()}
            {this.renderSliderA()}
            {this.renderButtonB()}
            {this.renderSliderB()}
            {this.renderButtonC()}
            {this.renderSliderC()}
          </div>
          <div>
            <Canvas a={this.state.a} b={this.state.b} c={this.state.c} />
          </div>
        </div>
      );
    }
  }
  
  class Canvas extends React.Component {
    constructor(props) {
      super(props);
      this.myRef = React.createRef();
    }
  
    Sketch = (p) => {
      p.setup = () => {
        p.createCanvas(710, 400);
      };
      p.draw = () => {
        p.background(0);
        p.frameRate(30);
        p.stroke(255);
        let a = (p.mouseX / p.width) * 90;
        p.theta = p.radians(a);
        p.translate(p.width / 2, p.height);
        p.line(0, 0, 0, -120);
        p.translate(0, -120);
        p.branch(this.props.c); // factor c
      };
  
      p.branch = (h) => {
        h *= this.props.a; // factor a
        if (h > this.props.b) {
          // factor b
          p.push();
          p.rotate(p.theta);
          p.line(0, 0, 0, -h);
          p.translate(0, -h);
          p.branch(h);
          p.pop();
  
          p.push();
          p.rotate(-p.theta);
          p.line(0, 0, 0, -h);
          p.translate(0, -h);
          p.branch(h);
          p.pop();
        }
      };
    };
  
    componentDidMount() {
      //this.myP5 = new p5(this.Sketch, this.myRef.current)
      this.myP5 = new p5(this.Sketch, this.myRef.current);
    }
  
    render() {
      return <div ref={this.myRef}></div>;
    }
  }
  export default Control;